header {
  width: 100%;
}
header nav {
  padding: 25px 0px;
}
header nav .nan {
  padding: 25px 0px;
}
header nav .nan p {
  font-size: 18px;
  font-weight: 400;
  line-height: 20px;
  color: #747474;
  text-align: left;
  margin-bottom: 10px;
}
@media (max-width: 767px) {
